//import * as url from "./url";
import { Message } from 'element-ui';
import { baseUrl } from './url';

$.ajaxSetup({
	success: function(data, textStatus, request) {
		if (data.returnCode !== '200') {
			Message({
				message: data.returnMsg,
				type: 'error'
			});
		}
	},
	complete:function (XMLHttpRequest,textStatus) {

	},
	headers: {
	},
	error: function (xhr) {
		if (xhr.status == '403') {
			window.location.href ='/login';
		}
		Message.error(xhr.responseText || '未知错误');
	}
});

export const ajaxGetCommon = (url, data) => {
	return $.ajax({
		url: baseUrl + url,
		type: 'GET',
		//contentType: "application/json;charset=utf-8",
		data: data,
	});
};

export const ajaxPostCommon = (url, data) => {
	return $.ajax({
		url: baseUrl + url,
		type: 'POST',
		data: data,
	});
};

export const ajaxUploadCommon = (url, data) => {
	return $.ajax({
		url: baseUrl + url,
		type: 'POST',
		processData: false,
        contentType: false,
		data: data,
	});
}




