import Vuex from 'vuex';
import Vue from 'vue';
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		sideMenu: [],
		articleLabels: [],
		columnList: [],
		infoAccount: []
	},
	mutations: {
		initSideMenu(state, data) {
			state.sideMenu = data;
		},

		initArticleLabel(state, data) {
			state.articleLabels = data;
		},

		initInfoAccount(state, data) {
			state.infoAccount = data;
		},

		initColumnList(state, data) {
			state.columnList = data;
		}
	}
})



