import Vue from "vue";
import Router from "vue-router";

const NotFound = r => require.ensure([], () => r(require("../views/404.vue")));
const Home = r => require.ensure([], () => r(require("../views/Home.vue")));
const Login = r => require.ensure([], () => r(require("../views/login.vue")));

const Message = r => require.ensure([], () => r(require("../views/message/index.vue")));
const UnCheckList = r => require.ensure([], () => r(require("../views/content/unCheckList.vue")));
const RecycleBin = r => require.ensure([], () => r(require("../views/content/recycleBin.vue")));
const ColumnList = r => require.ensure([], () => r(require("../views/content/columnList.vue")));

const Math = r => require.ensure([], () => r(require("../views/math/index.vue")));
const Target = r => require.ensure([], () => r(require("../views/target/index.vue")));
const Cate = r => require.ensure([], () => r(require("../views/tags/cate.vue")));
const Tag = r => require.ensure([], () => r(require("../views/tags/tag.vue")));
const ColumnMag = r => require.ensure([], () => r(require("../views/content/columnManage.vue")));
const SpecialMag = r => require.ensure([], () => r(require("../views/content/specialManage.vue")));
const AccountList = r => require.ensure([], () => r(require("../views/infoAccount/accountList.vue")));
const FunctionalUnit = r => require.ensure([], () => r(require("../views/functionalUnit/unitList.vue")));
const Advertisement = r => require.ensure([], () => r(require("../views/advertisement/list.vue")));
const SortRule = r => require.ensure([], () => r(require("../views/math/sortRuleList.vue")));

Vue.use(Router);

const router = new Router({
	routes: [
		{
			path: "/",
			component: Home,
			name: "home",
			redirect: '/unCheckList',
			meta: {
				title: "资讯后台管理"
			},
			children: [
				{
					path: 'message',
					component: Message,
					name: 'message',
					meta: {
						title: '信息管理'
					}
				},
				{
					path: 'unCheckList',
					component: UnCheckList,
					name: 'unCheckList',
					meta: {
						title: '待审核列表'
					}
				},
				{
					path: 'recycleBin',
					component: RecycleBin,
					name: 'recycleBin',
					meta: {
						title: '回收站'
					}
				},
				{
					path: '/columnList/:id',
					component: ColumnList,
					name: 'columnList',
					meta: {
						title: '栏目'
					}
				},
				{
					path: 'math',
					component: Math,
					name: 'math',
					meta: {
						title: '推荐算法参数配置'
					}
				},
				{
					path: 'infoAccount',
					component: AccountList,
					name: 'AccountList',
					meta: {
						title: '资讯号管理'
					}
				},
				{
					path: 'components',
					component: FunctionalUnit,
					name: 'unitList',
					meta: {
						title: '功能组件管理'
					}
				},
				{
					path: 'target',
					component: Target,
					name: 'target',
					meta: {
						title: '爬虫目标管理'
					}
				},
				{
					path: 'cate',
					component: Cate,
					name: 'cate',
					meta: {
						title: '标签分类管理'
					}
				},
				{
					path: 'tag',
					component: Tag,
					name: 'tag',
					meta: {
						title: '标签管理'
					}
				},
				{
					path: 'columnmanage',
					component: ColumnMag,
					name: 'columnmanage',
					meta: {
						title: '栏目管理'
					}
				},
				{
					path: 'specialmange',
					component: SpecialMag,
					name: 'specialmange',
					meta: {
						title: '专题管理'
					}
				},
				{
					path: 'advertisement',
					component: Advertisement,
					name: 'advertisement',
					meta: {
						title: '广告管理'
					}
				},
				{
					path: 'sortRule',
					component: SortRule,
					name: 'sortRule',
					meta: {
						title: '信息流规则管理'
					}
				},
			]
		},
		{
			path: "/login",
			component: Login,
			name: "login",
			meta: {
				title: "Cms登录系统"
			},
		},
		{
			path: "/404",
			component: NotFound,
			name: "404",
			hidden: true
		},
		{
			path: "*",
			hidden: true,
			redirect: { path: "/404" }
		}
	]
});

router.beforeEach((to, from, next) => {
	next();
	document.title = to.meta.title;
});

export default router;



