<template>
  <div class="kindeditor">
    <textarea :id="id" name="content">{{ outContent }}</textarea>
    <!-- 上传图片 -->
    <input id="uploadImageId" type="file" @change="uploadImg" accept="image/*" style="display: none">
    <!-- 上传视频的 dialog -->
    <el-dialog title="添加视频" :append-to-body="true" class="video-dialog" :visible.sync="videoConfig.dialog" width="500px">
      <el-form ref="form" label-width="120px">
        <el-form-item label="视频文件：">
          <el-upload class="avatar-uploader" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage" :data="{type: 2}" :show-file-list="false" :on-success="readyUploadVideo" :before-upload="beforeUploadVideo">
            <video v-if="videoConfig.url !='' && videoConfig.uploadFlag == false" :src="videoConfig.url" class="avatar" controls="controls">您的浏览器不支持视频播放</video>
            <i v-else-if="videoConfig.url =='' && videoConfig.uploadFlag == false" class="el-icon-plus avatar-uploader-icon"></i>
            <el-progress v-if="videoConfig.uploadFlag == true" type="circle" :percentage="videoConfig.uploadPercent" style="margin-top:30px;"></el-progress>
          </el-upload>
        </el-form-item>
        <el-form-item label="视频地址：">
          <el-input v-model="videoConfig.url" placeholder="请填写视频地址(小于10M)" style="width: 90%;"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer" align="center">
        <el-button type="primary">取 消</el-button>
        <el-button type="success" @click="insertVideoTag">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { ajaxPostCommon, ajaxUploadCommon } from "@/api/common";
import * as ajaxUrl from "@/api/url";

export default {
  name: "kindeditor",
  data() {
    return {
      ajaxUrl,
      editor: null,
      outContent: this.content,
      uploadServer: ajaxUrl.uploadArticleImage, // 上传地址
      videoConfig: {
        dialog: false,
        uploadFlag: false,
        uploadPercent: 0,
        url: "",
        videoListener: null
      }
    };
  },
  props: {
    content: {
      type: String,
      default: ""
    },
    id: {
      type: String,
      required: true
    },
    width: {
      type: String
    },
    height: {
      type: String
    },
    minWidth: {
      type: Number,
      default: 650
    },
    minHeight: {
      type: Number,
      default: 100
    },
    items: {
      type: Array,
      default: function() {
        return [
          "undo",
          "redo",
					"source",
					"link",
          "fontname",
          "formatblock",
          "bold",
          "underline",
          "fontsize",
          "forecolor",
          "hilitecolor",
          "justifyleft",
          "justifycenter",
          "justifyright",
          "insertorderedlist",
          "insertunorderedlist",
          "uploadImage",
          "uploadVideo",
          "clearStyle"
        ];
      }
    },
    noDisableItems: {
      type: Array,
      default: function() {
        return ["source", "fullscreen"];
      }
    },
    filterMode: {
      type: Boolean,
      default: true
    },
    htmlTags: {
      type: Object,
      default: function() {
        return {
          font: ["color", "size", "face", ".background-color"],
          span: ["style"],
          div: ["class", "align", "style"],
          table: [
            "class",
            "border",
            "cellspacing",
            "cellpadding",
            "width",
            "height",
            "align",
            "style"
          ],
          "td,th": [
            "class",
            "align",
            "valign",
            "width",
            "height",
            "colspan",
            "rowspan",
            "bgcolor",
            "style"
          ],
          a: ["class", "href", "target", "name", "style"],
          embed: [
            "src",
            "width",
            "height",
            "type",
            "loop",
            "autostart",
            "quality",
            "style",
            "align",
            "allowscriptaccess",
            "/"
          ],
          img: [
            "src",
            "width",
            "height",
            "border",
            "alt",
            "title",
            "align",
            "style",
            "/"
          ],
          video: ["src", "style", "controls", "poster"],
          hr: ["class", "/"],
          br: ["/"],
          "p,ol,ul,li,blockquote,h1,h2,h3,h4,h5,h6": ["align", "style"],
          "tbody,tr,strong,b,sub,sup,em,i,u,strike": []
        };
      }
    },
    wellFormatMode: {
      type: Boolean,
      default: true
    },
    resizeType: {
      type: Number,
      default: 2
    },
    themeType: {
      type: String,
      default: "default"
    },
    langType: {
      type: String,
      default: "zh-CN"
    },
    designMode: {
      type: Boolean,
      default: true
    },
    fullscreenMode: {
      type: Boolean,
      default: false
    },
    basePath: {
      type: String
    },
    themesPath: {
      type: String
    },
    pluginsPath: {
      type: String,
      default: ""
    },
    langPath: {
      type: String
    },
    minChangeSize: {
      type: Number,
      default: 5
    },
    loadStyleMode: {
      type: Boolean,
      default: true
    },
    urlType: {
      type: String,
      default: ""
    },
    newlineTag: {
      type: String,
      default: "p"
    },
    pasteType: {
      type: Number,
      default: 2
    },
    dialogAlignType: {
      type: String,
      default: "page"
    },
    shadowMode: {
      type: Boolean,
      default: true
    },
    zIndex: {
      type: Number,
      default: 811213
    },
    useContextmenu: {
      type: Boolean,
      default: true
    },
    syncType: {
      type: String,
      default: "form"
    },
    indentChar: {
      type: String,
      default: "\t"
    },
    cssPath: {
      type: [String, Array]
    },
    cssData: {
      type: String
    },
    bodyClass: {
      type: String,
      default: "ke-content"
    },
    colorTable: {
      type: Array
    },
    afterCreate: {
      type: Function
    },
    afterChange: {
      type: Function
    },
    afterTab: {
      type: Function
    },
    afterFocus: {
      type: Function
    },
    afterBlur: {
      type: Function
    },
    afterUpload: {
      type: Function
    },
    uploadJson: {
      type: String
    },
    fileManagerJson: {
      type: Function
    },
    allowPreviewEmoticons: {
      type: Boolean,
      default: true
    },
    allowImageUpload: {
      type: Boolean,
      default: true
    },
    allowFlashUpload: {
      type: Boolean,
      default: true
    },
    allowMediaUpload: {
      type: Boolean,
      default: true
    },
    allowFileUpload: {
      type: Boolean,
      default: true
    },
    allowFileManager: {
      type: Boolean,
      default: false
    },
    fontSizeTable: {
      type: Array,
      default: function() {
        return ["10px", "11px", "12px", "13px", "14px", "15px", "16px", "18px"];
      }
    },
    imageTabIndex: {
      type: Number,
      default: 0
    },
    formatUploadUrl: {
      type: Boolean,
      default: true
    },
    fullscreenShortcut: {
      type: Boolean,
      default: false
    },
    extraFileUploadParams: {
      type: Array,
      default: function() {
        return [];
      }
    },
    filePostName: {
      type: String,
      default: "imgFile"
    },
    fillDescAfterUploadImage: {
      type: Boolean,
      default: false
    },
    afterSelectFile: {
      type: Function
    },
    pagebreakHtml: {
      type: String,
      default: "<hr style=”page-break-after: always;” class=”ke-pagebreak” />"
    },
    allowImageRemote: {
      type: Boolean,
      default: true
    },
    autoHeightMode: {
      type: Boolean,
      default: false
    },
    fixToolBar: {
      type: Boolean,
      default: false
    },
    tabIndex: {
      type: Number
    }
  },
  methods: {
    insertHtml(val) {
      this.editor.insertHtml(val);
    },

    // 上传图片
    uploadImg(e) {
      let _this = this;
      let formData = new FormData();
			formData.append("file", e.target.files[0]);
      ajaxUploadCommon(this.uploadServer, formData).then(res => {
        const img = `<img src="${res.result}" style="width:100%"/>`;
				_this.insertHtml(img);
				document.getElementById("uploadImageId").value = "";
      });
    },

    beforeUploadVideo(file) {
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (
        [
          "video/mp4",
          "video/ogg",
          "video/flv",
          "video/avi",
          "video/wmv",
          "video/rmvb"
        ].indexOf(file.type) == -1
      ) {
        this.$message.error("请上传正确的视频格式");
        return false;
      }
      if (!isLt10M) {
        this.$message.error("上传视频大小不能超过10MB哦!");
        return false;
      }
    },

    readyUploadVideo(res, file) {
      this.videoConfig.uploadFlag = true;
      if (res.returnCode === "200") {
        this.uploadOnProcess(res);
      }
    },

    uploadOnProcess(res) {
      this.videoConfig.videoListener = setInterval(() => {
        ajaxPostCommon(ajaxUrl.uploadProgress, { key: res.result }).then(
          res => {
            const uploadPercent = res.result.progress - 0,
              url = res.result.url;
            if (uploadPercent <= 100 && !url) {
              this.videoConfig.uploadPercent = uploadPercent;
            } else {
              this.videoConfig.uploadFlag = false;
              this.videoConfig.url = url;
              clearInterval(this.videoConfig.videoListener);
            }
          }
        );
      }, 500);
    },

    insertVideoTag() {
			const VIDEO_POSTER = "https://tac-cdn.zhongan.com/wxapp/activity/double_99/info_poster.jpg";
      if (!this.videoConfig.url) {
        this.$message.error('请填写视频地址');
        return false;
      }
      const videoHTML = `<video src="${this.videoConfig.url}" controls="controls" poster=${VIDEO_POSTER} style="width:100%"></video><p><br /></p>`;
      this.editor.insertHtml(videoHTML);
      this.videoConfig.url = "";
      this.videoConfig.dialog = false;
    },

    // 去除 富文本格式
    clearRichTags(text) {
      text = text.replace(/(<\/?)([a-z]+)(\s[\w\W]*?)?(>)/gi, function(
        s0,
        s1,
        s2,
        s3,
        s4
      ) {
        if (!/^(img)$/.test(s2)) return "";
        if (s3)
          s3 = s3.replace(/\s+(id|class|style)(=(([\"\']).*?\4|\S*))?/g, "");
        return s1 + s2 + (s3 || "") + s4;
      });
      text = text.replace(/(\n)/g, "");
      text = text.replace(/(\t)/g, "");
      text = text.replace(/(\r)/g, "");
      return text;
    }
  },

  watch: {
    content(val) {
      this.editor && val !== this.outContent && this.editor.html(val);
    },
    outContent(val) {
      this.$emit("update:content", val);
      this.$emit("on-content-change", val);
    }
  },

  mounted() {
    var _this = this;
    // 插入图片
    KindEditor.plugin("uploadImage", function(K) {
      var editor = this,
        name = "uploadImage";
      editor.clickToolbar(name, function() {
        document.querySelector("#uploadImageId").click();
        _this.$emit("uploadImage");
      });
    });
    // 插入视频
    KindEditor.plugin("uploadVideo", function(K) {
      var editor = this,
        name = "uploadVideo";
      editor.clickToolbar(name, function() {
         _this.videoConfig.dialog = true;
      });
    });
    // 清空样式
    KindEditor.plugin("clearStyle", function(K) {
      var editor = this,
        name = "clearStyle";
      editor.clickToolbar(name, function() {
        editor.html(editor.text());
        _this.$emit("clearStyle");
      });
    });

    // 实例化 kindEditor
    _this.editor = window.KindEditor.create("#" + this.id, {
      width: _this.width,
      height: _this.height,
      minWidth: _this.minWidth,
      minHeight: _this.minHeight,
      items: _this.items,
      noDisableItems: _this.noDisableItems,
      filterMode: _this.filterMode,
      htmlTags: _this.htmlTags,
      wellFormatMode: _this.wellFormatMode,
      resizeType: _this.resizeType,
      themeType: _this.themeType,
      langType: _this.langType,
      designMode: _this.designMode,
      fullscreenMode: _this.fullscreenMode,
      basePath: _this.basePath,
      themesPath: _this.cssPath,
      pluginsPath: _this.pluginsPath,
      langPath: _this.langPath,
      minChangeSize: _this.minChangeSize,
      loadStyleMode: _this.loadStyleMode,
      urlType: _this.urlType,
      newlineTag: _this.newlineTag,
      pasteType: _this.pasteType,
      dialogAlignType: _this.dialogAlignType,
      shadowMode: _this.shadowMode,
      zIndex: _this.zIndex,
      useContextmenu: _this.useContextmenu,
      syncType: _this.syncType,
      indentChar: _this.indentChar,
      cssPath: _this.cssPath,
      cssData: _this.cssData,
      bodyClass: _this.bodyClass,
      colorTable: _this.colorTable,
      afterCreate: _this.afterCreate,
      afterChange: function() {
        _this.afterChange;
        _this.outContent = this.html();
      },
      afterTab: _this.afterTab,
      afterFocus: _this.afterFocus,
      afterBlur: _this.afterBlur,
      afterUpload: _this.afterUpload,
      uploadJson: _this.uploadJson,
      fileManagerJson: _this.fileManagerJson,
      allowPreviewEmoticons: _this.allowPreviewEmoticons,
      allowImageUpload: _this.allowImageUpload,
      allowFlashUpload: _this.allowFlashUpload,
      allowMediaUpload: _this.allowMediaUpload,
      allowFileUpload: _this.allowFileUpload,
      allowFileManager: _this.allowFileManager,
      fontSizeTable: _this.fontSizeTable,
      imageTabIndex: _this.imageTabIndex,
      formatUploadUrl: _this.formatUploadUrl,
      fullscreenShortcut: _this.fullscreenShortcut,
      extraFileUploadParams: _this.extraFileUploadParams,
      filePostName: _this.filePostName,
      fillDescAfterUploadImage: _this.fillDescAfterUploadImage,
      afterSelectFile: _this.afterSelectFile,
      pagebreakHtml: _this.pagebreakHtml,
      allowImageRemote: _this.allowImageRemote,
      autoHeightMode: _this.autoHeightMode,
      fixToolBar: _this.fixToolBar,
      tabIndex: _this.tabIndex
    });
  }
};
</script>

<style scoped>
video {
  width: 100%;
  max-width: 300px;
}
</style>



