<template>
	<layout>
		<div slot="header" height="50">
			<za-nav :header="header">
				<slot name="nav"></slot>
			</za-nav>
		</div>
		<div class="content-padding" :style="contentStyle">
			<slot></slot>
		</div>
	</layout>
</template>
<script>
	export default {
		props: ['header', 'scroll'],
		computed: {
			contentStyle() {
				let classes = {};
				if (this.scroll) {
					classes.overflow = 'auto'
				}
				return classes;
			}
		}
	}
</script>
<style scoped>
	.content-padding{
		background: #fff;
	}
</style>



