<template>
	<layout ref="layout">
		<div slot="header" class="searchbar" v-if="$slots.searchBar">
			<slot name="searchBar"></slot>
		</div>
		<layout>
			<div slot="header" class="actionbar" v-if="$slots.actionbar">
				<slot name="actionbar"></slot>
			</div>
			<div style="height: 100%">
				<el-table
					height="aaa"
					ref="elTable"
					v-loading="isLoading"
					border
					v-bind="$props"
					@selection-change="selectionChange"
					@sort-change="handleSortChange"
					@filter-change="handleFilterChange"
					style="width: 100%;height: 100%"
				>
					<slot></slot>
				</el-table>
			</div>
			<div slot="footer" height="52" v-if="total">
				<el-col :span="24" class="toolbar pageagation">
					<el-pagination
				      	@size-change="handleSizeChange"
				      	@current-change="handleCurrentChange"
				      	:current-page="currentPage"
				      	:page-sizes="[10, 40, 100, 200]"
				      	:page-size="pageSize"
				      	layout="total, sizes, prev, pager, next"
				      	:total="total">
				    </el-pagination>
				</el-col>
			</div>
		</layout>
	</layout>
</template>
<script>

	let params = {
		// page  size  column{order, prop}  filters: column-key selectedFilter
	};
	export default {
		props: {
			data: {
				type: Array,
				default: function _default() {
					return [];
				}
			},
			width: [String, Number],
			height: [String, Number],
			maxHeight: [String, Number],
			fit: {
				type: Boolean,
				default: true
			},
			stripe: Boolean,
			border: Boolean,
			rowKey: [String, Function],
			context: {},
			showHeader: {
				type: Boolean,
				default: true
			},
			rowClassName: [String, Function],
			rowStyle: [Object, Function],
			highlightCurrentRow: Boolean,
			currentRowKey: [String, Number],
			emptyText: String,
			expandRowKeys: Array,
			defaultExpandAll: Boolean,
			defaultSort: Object,

			isLoading: {
				default: false,
				type: Boolean
			},

			total: Number
		},
		data() {
			return {
				currentPage: 1,
				auto: 400,
				pageSize: 10,
				$props: this.$props
			}
		},
		methods: {
			refreshLayout() {
				this.$refs.layout.refresh();
			},
			handleCurrentChange(currentPage) {
				if (params.page === undefined && currentPage === 1) {
					return false;
				}
				params.page = currentPage;
				this.currentPage = currentPage;
				this.$emit('action-change', params);
			},
			handleSizeChange(size) {
				params.rows = size;
				params.page = 1;
				this.pageSize = size;
				this.currentPage = 1;
				this.$emit('action-change', params);
			},
			handleSortChange(column) {
				params.column = {
					order: column.order,
					prop: column.prop
				};
				this.$emit('action-change', params);
			},
			handleFilterChange(filters) {
				params.filters = filters;
				this.$emit('action-change', params);
			},
			selectionChange(sels) {
				this.$emit('selection-change', sels);
			},
			resetCurrentPage() {
				if (this.currentPage !== 1) {
					this.currentPage = 1;
					return true;
				} else {
					return false;
				}
			}
		},
		mounted() {
			params = {};
		}
	}
</script>
<style scoped>
	.searchbar {
		background-color: #ffffff;
	    margin: 15px 0;
	    padding: 15px;
	    overflow: hidden;
	}
	.toolbar {
		padding: 5px;
		margin-bottom: 0;
		background-color: #ffffff;
		border: none;
	}
	.actionbar {
	    background-color: #ffffff;
    	padding: 5px 15px;
	}
</style>



