<template>
	<div class="za-layout" :style="wrapStyle">
		<div class="za-layout__header clearfix" ref="header" :style="headerStyle" v-if="$slots.header">
			<slot name="header"></slot>
		</div>
		<div class="za-layout__content clearfix" ref="content" :style="contentStyle" v-if="$slots.default">
			<slot></slot>
		</div>
		<div class="za-layout__footer clearfix" ref="footer" :style="footerStyle" v-if="$slots.footer">
			<slot name="footer"></slot>
		</div>
	</div>
</template>
<script>
	import * as Util from '../../assets/js/util.js'
	export default {
		props: ['scroll'],
		data() {
			return {
				headerHeight: 0,
				contentHeight: '',
				footerHeight: 0,
			}
		},
		computed: {
			wrapStyle() {
				if (this.scroll) {
					return {
						'overflow': 'auto'
					}
				}
			},
			headerStyle() {
				return {
					height: this.headerHeight + 'px'
				}
			},
			footerStyle() {
				return {
					height: this.footerHeight + 'px'
				}
			},
			contentStyle() {
				if (this.scroll) {
					return {}
				}
				let str = '100%';
				if (this.headerHeight && !isNaN(this.headerHeight)) {
					str += ` - ${this.headerHeight}px`
				}
				if (this.footerHeight && !isNaN(this.footerHeight)) {
					str += ` - ${this.footerHeight}px`
				}
				return {
					height: `calc( ${str} )`
				}
			}
		},
		methods: {
			refresh() {
				this.$nextTick( () => {
					let height = 0;
					if (this.$slots.header) {
						height = parseInt(this.$refs.header.firstElementChild.getAttribute('height')) || 0;
						if (height) {
		 					this.headerHeight = height;
						} else {
							this.headerHeight = Util.getOuterHeight(this.$refs.header.firstElementChild, true)
						}
					}
					if (this.$slots.footer) {
						height =  parseInt(this.$refs.footer.firstElementChild.getAttribute('height')) || 0;
						if (height) {
		 					this.footerHeight = height;
						} else {
							this.footerHeight = this.$refs.footer.offsetHeight
						}
					}
				})
			}

		},
		mounted() {
			this.refresh();
		}
	}
</script>
<style scoped>
	.za-layout {
		width: 100%;
		height: 100%;
		flex-direction: column;
		overflow-y: hidden;
	}
</style>


